.header {
  //height: 65px;
  background: #4f4fd9;
  box-shadow: 0px 2px 2px -1px rgba(0,0,0,0.44);

  &:after {
    content: "";
    clear: both;
    display: block;
  }

  &__logo {
    float: left;
    display: inline-block;
    padding: 10px;
    margin: 12px;
    background: #ff9c3f;
  }

}

.header-menu {
  float: left;
  display: inline-block;
  font-family: 'gothic', sans-serif;
  font-size: 20px;

  &__item {
    display: inline-block;
    text-decoration: none;
    color: #fff;
    border-top: 2px solid #4f4fd9;
    padding: 19px 0;
    margin: 0 14px 0 14px;

    &_active {
      border-color: #ff9c3f;
    }

    &:hover {
      color: #e9e9e9;
    }

  }

}

.header-search {
  display: inline-block;
  float: right;
  background: #fff;
  border-radius: 3px;
  margin-top: 18px;

  &__input {
    border-radius: 3px;
    border: 0;
    padding: 3px 10px;
    font-family: 'gothic', sans-serif;
    font-size: 16px;
  }

  &__btn {
    position: relative;
    top: 1px;
    border: 0;
    background: #fff;
    cursor: pointer;
  }

}

.header-city-select {
  display: inline-block;
  float: right;
  font-family: 'gothic', sans-serif;
  font-size: 12px;
  padding: 15px 20px 0 20px;

  &__name {
    color: #fff;
    text-decoration: none;
    border-bottom: 1px dotted #fff;

    &:hover {
      color: #e9e9e9;
    }

  }

  &__phone {
    margin-top: 5px;
    color: #fff;
    font-weight: bold;
  }

}
