.detail-container {
  margin: 0 auto;
  max-width: 1100px;
  padding: 0 25px;

  &:after {
    content: "";
    display: table;
    clear: both;
  }

}

.detail {
  font-family: 'gothic', sans-serif;
  padding-bottom: 25px;

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  &__head {
    padding: 20px 0;
  }

  &__delivery-option {
    display: inline-block;
    font-size: 24px;
    color: #7f7f7f;
  }

  &__number {
    display: inline-block;
    float: right;
    font-size: 24px;
    color: #7f7f7f;
  }

}

.detail-item {
  position: relative;
  display: inline-block;
  float: left;
  width: 50%;
  padding-right: 15px;
  color: #7f7f7f;

  &__img {
    max-width: 135px;
    max-height: 135px;
    overflow:hidden;
    float: left;

    & > img {
      width: 200px;
    }

  }

  &__box {
    margin-left: 155px;
    padding-bottom: 15px;
  }

  &__additionally {
    display: block;
    font-size: 16px;
  }

  &__star {
    position: absolute;
    top: 0px;
    right: 15px;
  }

  &__price {
    display: inline-block;
    font-size: 30px;
    padding-bottom: 5px;
  }

  &__price-val {
    font-size: 12px;
    padding-left: 10px;
  }

  &__fr-box {
    display: inline-block;
    float: right;
  }

  &__commission {
    display: block;
    font-size: 12px;
  }

  &__vacancy {
    display: block;
    font-size: 12px;
  }

  &__btn-box {
    float: left;
    width: 100%;
  }

  &__btn-get {
    display: inline-block;
    float: left;
    font-family: 'gothic', sans-serif;
    padding: 6px 46px;
    border-radius: 3px;
    color: #fff;
    background: #4f4fd9;
    cursor: pointer;
    margin-top: 15px;
    box-shadow: 0px 2px 2px -1px rgba(0,0,0,0.44);
    transition: all 0.3s;

    &:hover {
      background: #5353e6;
    }

    &:active {
      background: #4d4dd5;
    }

  }

}

.detail-map-box {
  float: left;
  width: 50%;
  height: 190px;
  padding-left: 15px;
}

#detail-map {
  display: block;
  width: 100%;
  height: 100%;
  background: #b2b2b2;
}

.detail-rent-row {
  box-shadow: inset 0px 0px 7px 0px rgba(0,0,0,0.4);
  background: #f5f5f5;
}

.detail-rent-container {
  margin: 0 auto;
  max-width: 1100px;
  padding: 0 25px;
  font-family: 'gothic', sans-serif;
}

.detail-rent-form {

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  &_col {
    float: left;
    width: 50%;
  }

  &_col-1-2 {
    float: left;
    width: 50%;
    padding-left: 15px;
  }

  &__inp-box {
    width: 50%;
    float: left;
    padding: 25px 0;

    &_pdr {
      padding-right: 10px;
    }

    &_pdl {
      padding-left: 10px;
    }

    & > input {
      width: 100%;
      border: 1px solid #7f7f7f;
      border-radius: 3px;
      padding: 5px 10px;
      background: #f5f5f5;
    }
  }

  &__text-box {
    width: 100%;
    padding-bottom: 25px;

    & > textarea {
      width: 100%;
      height: 92px;
      border: 1px solid #7f7f7f;
      border-radius: 3px;
      padding: 5px 10px;
      background: #f5f5f5;
    }

  }

  &__inp-code {
    width: 100%;
    border: 1px solid #7f7f7f;
    border-radius: 3px;
    padding: 5px 10px;
    background: #f5f5f5;
    margin-top: 25px;
  }

  &__btn-get-code {
    display: block;
    text-align: center;
    font-family: 'gothic', sans-serif;
    padding: 6px 46px;
    border-radius: 3px;
    color: #fff;
    background: #ff9c3f;
    cursor: pointer;
    margin-top: 25px;
    box-shadow: 0px 2px 2px -1px rgba(0,0,0,0.44);
    transition: all 0.3s;

    &:hover {
      background: #e98e39;
    }

    &:active {
      background: #de8837;
    }
  }

  &__btn-submit {
    display: block;
    text-align: center;
    font-family: 'gothic', sans-serif;
    padding: 6px 46px;
    border-radius: 3px;
    color: #fff;
    background: #4f4fd9;
    cursor: pointer;
    margin-top: 25px;
    box-shadow: 0px 2px 2px -1px rgba(0,0,0,0.44);
    transition: all 0.3s;

    &:hover {
      background: #5353e6;
    }

    &:active {
      background: #4d4dd5;
    }

  }

  &__msg-text {
    font-family: 'gothic', sans-serif;
    border: 1px dotted #bfbfbf;
    border-radius: 3px;
    padding: 5px;
    margin-top: 25px;
  }

}

.detail-info {

  &:after {
    content: "";
    display: table;
    clear: both;
  }

  &__col {
    float: left;
    width: 50%;
    padding-top: 25px;

    &_pdnr {
      padding-left: 15px;
    }

  }

  &__player {
    padding-top: 25px;
    width: 100%;
    height: 400px;
  }

}
