$text-color: #3f3f3f;
$range-label-color: #3f3f3f;
$range-label-color-hover: #1565C0;
$link-color: #3f3f3f;
$link-color-hover: #0d47a1;
$checkbox-color: #3f3f3f;
$checkbox-color-hover: #1565C0;
$radio-color: #3f3f3f;
$radio-color-hover: #1565C0;

.rudi-filter-wrap {
  position: fixed;
  right: 0;
  height: calc(100% - 65px);
  background: #fff;
}

.rudi-filter-scroll-wrap {
  display: inline-block;
  width: 300px;
  height: calc(100% - 61px);
  overflow: hidden;
  border-right: 1px dotted #eee;
}

.rudi-filter {
  border-bottom: 1px solid #eee;
  background: #fff;
  transition: all 0.2s;

  &:hover {
    background: #f5f5f5;
  }

  &:first-child {
    border-radius: 3px 3px 0 0;
  }

  &:last-child {
    border-radius: 0 0 3px 3px;
    border-bottom: none;
  }

  &__header {
    position: relative;
    padding: 10px 10px 10px 20px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all .2s;

    &:hover {
      background-color: #e7e7e7;
    }

    &:after {
      position: absolute;
      top: 18px;
      left: 5px;
      width: 12px;
      height: 12px;
      margin-top: -4px;
      content: "";
      text-align: center;
      background-image: url('../../images/right-arrow.png');
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }


    &_closed {

      &:after {
        transform: rotate(180deg);
      }

    }
  }

  &__title {
    position: relative;
    padding: 0;
    margin: 0;
    font: 500 18px 'gothic', sans-serif;
    color: #3f3f3f;
    transition: color .2s;

    &:hover {
      color: #0D47A1;
    }

    &_active {

      &:before {
        position: absolute;
        top: 10px;
        left: -20px;
        width: 0;
        height: 0;
        margin-top: -6px;
        content: '';
        border: solid transparent;
        border-width: 6px;
        border-color: rgba(89, 89, 89, 0);
        border-left-color: #fec151;
        pointer-events: none;
      }

    }

  }

  &__body {
    padding: 5px 20px;

    &_closed {
      display: none;
    }

  }

  &-link {
    display: block;
    text-decoration: none;
    margin: 3px 0;
    font: 400 16px 'gothic', sans-serif;
    color: $link-color;
    transition: color .2s;

    &:hover {
      color: $link-color-hover;
    }
  }

  &-checkbox {
    position: relative;
    display: block;
    margin: 3px 0;

    &__label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
      font: 400 16px 'gothic', sans-serif;
      color: $checkbox-color;
      border-radius: 3px;
      transition: all .2s;

      &:after {
        content: "";
        clear: both;
        display: block;
      }

      & span {
        float: right;
        color: #777;
      }

      &:hover {
        background: #f6f6f6;
        color: $checkbox-color-hover;
      }

    }

    &__input {
      position: absolute;
      top: 3px;
      margin-left: -20px;
      outline: none;
    }
  }

  &-radio {
    position: relative;
    display: block;
    margin: 3px 0;

    &__label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
      font: 400 16px 'gothic', sans-serif;
      color: $radio-color;
      transition: color .2s;

      &:hover {
        color: $radio-color-hover;
      }

    }

    &__input {
      position: absolute;
      top: 3px;
      margin-left: -20px;
      outline: none;
    }
  }

  &-range {
    position: relative;
    display: block;
    font: 400 16px 'gothic', sans-serif;
    color: $text-color;

    &__label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      color: $range-label-color;
      transition: color .2s;

      &:hover {
        color: $range-label-color-hover;
      }

    }

    &__line {

      &-input {
        display: inline-block;
        position: relative;
        width: 100%;
        background-color: #fff;
        border: 1px #CFD8DC solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 10px;
        transition: border-color .2s;

        &:focus {
          border-color: #0D47A1;
        }
      }

    }

    &__group {

      &:after {
        content: "";
        clear: both;
        display: block;
      }

      &-input {
        display: inline-block;
        position: relative;
        width: 49%;
        background-color: #fff;
        border: 1px #CFD8DC solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 10px;
        transition: border-color .2s;

        &:focus {
          border-color: #0D47A1;
        }

        &:last-child {
          float: right;
        }

      }

    }

  }

  &-btn-all {
    display: inline-block;
    margin: 10px 0;
    font: 400 12px 'gothic', sans-serif;
    color: #757575;
    background: #f6f6f6;
    border-radius: 3px;
    text-decoration: none;
    padding: 5px 10px;
    transition: all .2s;

    &:hover {
      color: #fff;
      background-color: #1565C0;
    }

    &:active {
      background-color: #0D47A1;
    }
  }

  &__btn {
    position: relative;
    text-align: center;
    text-decoration: none;
    display: block;
    width: 100%;
    font-size: 0.875em;
    font-weight: 300;
    background-color: #fff;
    color: #444;
    border: 1px #b1b1b1 solid;
    border-radius: 3px;
    padding: 5px 10px;
    outline: none;

    &:hover {
      background-color: #f7f7f7;
    }

    &:active {
      background-color: #f1f1f1;
    }
  }

  &__slider {
    width: 100%;
    height: 20px;
    margin-bottom: 5px;
    z-index: 0;

    & .handle {
      font-size: 0.9em;
      text-align: center;
      background-color: #fff;
      color: white;
      cursor: pointer;
      border: 1px solid #909eab;
      border-radius: 50%;
      z-index: 0;
    }

    & .handle.active {
      background-color: #f1f1f1;
      border-color: #2196F3;
    }

    & .bar {
      position: relative;
      background: #ddd;
      cursor: pointer;
    }

    & .bar.bar-1 {
      background: #b1b1b1;
    }

    & .bar.bar-2 {
      background: #ddd;
    }

    & .bar {
      top: 8px;
      height: 1px;
    }

    & .handle {
      width: 18px;
      height: 18px;
    }

  }
}

&.rudi-filter-controls {
  padding: 15px;
  background: #fff;

  &__btn-show {
    display: inline-block;
    padding: 5px 22px;
    text-align: center;
    font: 400 16px 'gothic', sans-serif;
    color: #fff;
    background: #4f4fd9;
    border-radius: 3px;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      border-color: #5c5cff;
    }

    &:active {
      background-color: #3e3eac;
    }

  }

  &__btn-additionally {
    display: inline-block;
    padding: 5px 22px;
    margin-left: 10px;
    text-align: center;
    font: 400 16px 'gothic', sans-serif;
    color: #fff;
    background: #ff9c3f;
    border-radius: 3px;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      border-color: #ff9c3f;
    }

    &:active {
      background-color: #df8837;
    }

  }

}
